#about {
  margin: 3rem;
}

blockquote {
  color: var(--text-primary);
  font-size: 1.5rem;
  padding: 3rem;
  text-align: center;
}

.p_div {
  font-size: 1.5rem;
  padding: 3rem;
}

#about > div {
  display: flex;
  justify-content: center;
  align-items: center;
}

#profile_pic {
  border-radius: 50%;
}

.stats {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  margin: 3rem;
}

.stats > img {
  padding: 3rem;
}